import Parser from 'html-react-parser';
import { useState } from 'react';
import ReactQuill from 'react-quill';
import 'react-quill/dist/quill.snow.css';
import { useNavigate } from 'react-router-dom';
import './PostWritePage.css';

const PostWritePage = () => {
    const myColors = [
        'purple',
        '#785412',
        '#452632',
        '#856325',
        '#963254',
        '#254563',
        'white',
    ];
    const modules = {
        toolbar: [
            [{ header: [1, 2, 3, 4, 5, 6, false] }],
            ['bold', 'italic', 'underline', 'strike', 'blockquote'],
            [{ align: ['right', 'center', 'justify'] }],
            [{ list: 'ordered' }, { list: 'bullet' }],
            ['link', 'image'],
            [{ color: myColors }],
            [{ background: myColors }],
        ],
    };

    const formats = [
        'header',
        'bold',
        'italic',
        'underline',
        'strike',
        'blockquote',
        'list',
        'bullet',
        'link',
        'color',
        'image',
        'background',
        'align',
    ];

    const [title, setTitle] = useState<string>('');
    const [editorText, setEditorText] = useState<string>('');

    const handleProcedureContentChange = (content: string) => {
        setEditorText(content);
    };

    // console.log(editorText, title);

    const navigate = useNavigate();

    const goBack = () => {
        navigate(-1);
    };

    // @TODO: post   
    const writePost = () => {};

    return (
        <div className="grid grid-cols-2">
            {/*  -  */}
            <div className="h-screen flex flex-col">
                {/*  => ,    */}
                <div className="h-15 flex justify-between my-2 mx-1 text-sm">
                    <button
                        type="button"
                        className="text-gray-800 mx-3 my-2 rounded-md hover:bg-gray-100"
                        onClick={goBack}
                    >
                        
                    </button>
                    <button
                        type="button"
                        className="text-white bg-ml-pink-200 px-3 py-2 rounded-md hover:bg-ml-pink-300"
                        onClick={writePost}
                    >
                        
                    </button>
                </div>
                {/*  */}
                <div className="h-16">
                    <input
                        className="text-4xl m-2 w-full"
                        type="text"
                        placeholder=" "
                        value={title}
                        onChange={(e) => setTitle(e.target.value)}
                    />
                </div>
                {/*  */}
                <ReactQuill
                    theme="snow"
                    modules={modules}
                    formats={formats}
                    value={editorText}
                    placeholder=" "
                    onChange={handleProcedureContentChange}
                />
            </div>
            {/*  =  */}
            <div className="p-8 w-full text-ellipsis overflow-scroll">
                <div className="text-5xl pb-4">{title}</div>
                <div>{Parser(editorText)}</div>
            </div>
        </div>
    );
};

export default PostWritePage;
